			newImg: '',
			// 用户新昵称
			newName: ''
		}
	},
	onShow() {
		// 用户状态的切换
		// const userToken = uni.getStorageSync("userToken")

		// if(userToken !== null & userToken !== '' & userToken !== undefined) {
